import Mock from 'mockjs'

// 定义菜单数据
const allMenus = [
  {
    path: '/home',
    id: 1,
    name: 'home',
    label: '首页',
    icon: 's-home',
    url: 'Home/Home',
    permission: 'all'
  },
  {
    id: 2,
    path: '/home/mall',
    name: 'mall',
    label: '商品管理',
    icon: 'video-play',
    url: 'MallManage/MallManage',
    permission: 'all'
  },
  {
    id: 3,
    path: '/home/user',
    name: 'user',
    label: '用户管理',
    icon: 'user',
    url: 'UserManage/UserManage',
    permission: 'admin'
  },

  {
    label: '其他',
    icon: 'location',
    path: 'other',
    permission: 'all',
    children: [
      {
        id: 4,
        path: '/home/other/1',
        name: 'page1',
        label: '页面1',
        icon: 'setting',
        url: 'Other/PageOne',
        permission: 'all'
      },
      {
        id: 5,
        path: '/home/other/2',
        name: 'page2',
        label: '页面2',
        icon: 'setting',
        url: 'Other/PageTwo',
        permission: 'all'
      }
    ]
  }
]

// 封装菜单获取逻辑
export default {
  getMenu: (role = 'guest') => {
    try {
      // 根据角色过滤菜单
      const filteredMenus = role.body === 'admin'
        ? allMenus
        : allMenus.filter(menu => menu.permission === 'all' || menu.permission === role.body)

      return {
        code: 200,
        message: '获取菜单成功',
        data: filteredMenus
      }
    } catch (error) {
      return {
        code: 400,
        message: '获取菜单失败',
        error: error.message
      }
    }
  }
}


