<template>
  <el-form label-width="70px" class="login-container" ref="form" :model="form" :rules="rules">
    <h3 class="login_title">系统登录</h3>
    <el-form-item label="用户名" prop="username">
      <!-- 数据的双向绑定 -->
      <el-input v-model="form.username" placeholder="请输入账号"></el-input>
    </el-form-item>
    <el-form-item label="密码" prop="username">
      <!-- 数据的双向绑定 -->
      <el-input type="password" v-model="form.password" placeholder="请输入密码"></el-input>
    </el-form-item>
    <el-form-item label-width="0">
      <el-button type="primary" @click="login" class="btn">登录</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { login, getMenu } from '@/api'

export default {
  data() {
    return {
      form: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { required: true, trigger: 'blur', messsage: '请输入用户名' }
        ],
        password: [
          { required: true, trigger: 'blur', messsage: '请输入密码' }
        ]
      }
    }
  },
  methods: {
    login() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          localStorage.setItem('name', this.form.username);
          login(this.form).then(({ data }) => {
            if (data.code == 20000) {
              localStorage.setItem('token', data.data.token);
              this.getMenu(data.data.role)
              this.$router.push('/home')
            }
            else {
              this.$message.error(data.data.message)
            }
          })
        }
      })
    },
    getMenu(role) {
      getMenu(role).then(({ data }) => {
        localStorage.setItem('menus', JSON.stringify(data.data));
      })
    },
    handleLogin() {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.loading = true;
          // 使用你提供的menuService获取菜单
          const role = this.getRoleByCredentials(this.loginForm.username, this.loginForm.password);

          if (role) {
            // 模拟登录成功
            setTimeout(() => {
              const user = {
                username: this.loginForm.username,
                role: role,
                name: role === 'admin' ? '管理员' : '普通用户'
              };

              // 存储用户信息
              localStorage.setItem('admin-user', JSON.stringify(user));

              // 获取菜单
              this.getMenu(role)
                .then(menuRes => {
                  // 存储菜单数据
                  localStorage.setItem('admin-menus', JSON.stringify(menuRes.data));

                  // 模拟token
                  localStorage.setItem('admin-token', `token_${Date.now()}`);

                  this.$message.success('登录成功');

                  // 跳转到首页
                  setTimeout(() => {
                    this.$router.push('/home');
                  }, 1000);
                })
                .catch(err => {
                  this.$message.error('获取菜单失败');
                  console.error(err);
                })
                .finally(() => {
                  this.loading = false;
                });
            }, 1000);
          } else {
            // 登录失败
            this.$message.error('用户名或密码错误');
            this.loading = false;
          }
        } else {
          this.$message.warning('请检查表单信息');
          return false;
        }
      });
    },
    // 根据用户名和密码获取角色（模拟登录验证）
    getRoleByCredentials(username, password) {
      // 模拟用户数据库
      const users = {
        admin: { password: 'admin123', role: 'admin' },
        user: { password: 'user123', role: 'user' }
      };

      const user = users[username];
      if (user && user.password === password) {
        return user.role;
      }
      return null;
    },
    // 使用你提供的menuService获取菜单

  }
}
</script>

<style lang="less" scoped>
.text-center {
  display: flex;
  justify-content: center;
}

.btn {
  width: 50%;
}

.login-container {
  width: 350px;
  border: 1px solid #eaeaea;
  margin: 180px auto;
  padding: 35px 35px 15px 35px;
  background-color: #fff;
  border-radius: 15px;
  box-shadow: 0 0 25px #cac6c6;
  box-sizing: border-box;

  .login_title {
    text-align: center;
    margin-bottom: 40px;
    color: #505458;
  }

  el-input {
    width: 198px;
  }

}
</style>