<template>
  <div>
    <div class="col">
      <el-button @click="Add">+ new</el-button>
      <Dialog :modelType="modelType" ref="Dialog" :dialogVisible.sync="dialogVisible" :rows="row"
        @submit="handleFormSubmitted">
      </Dialog>
      <div class="right">
        <el-input class="ipt" placeholder="请输入内容" v-model="input" clearable @keyup.enter="getList">
        </el-input>
        <el-button @click="getList" type="primary">search</el-button>
      </div>
    </div>
    <el-table stripe fit highlight-current-row :data="tableData" style="width: 100%">

      <el-table-column prop="name" label="姓名" width="180">
      </el-table-column>
      <el-table-column prop="age" label="年龄" width="180">
      </el-table-column>
      <el-table-column prop="sex" label="性别">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.sex == 1 ? '男' : '女' }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="birth" label="出生日期" width="180">
      </el-table-column>
      <el-table-column prop="addr" label="地址" width="180">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" @click="edit(scope.row)">编辑</el-button>
          <el-button type="danger" size="mini" @click="del(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import Dialog from './Dialog.vue';
import { getUser, delUser } from '@/api'
export default {
  components: {
    Dialog
  },
  data() {
    return {
      dialogVisible: false,
      input: '',
      tableData: [],
      modelType: 0,
      row: {}
    }
  },
  methods: {
    handleFormSubmitted(status) {
      if (status) this.getList()
    },
    Add() {
      this.dialogVisible = true
      this.modelType = 0
      this.row = {}
    },
    del(row) {
      this.$confirm('此操作将永久删除, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        delUser({ id: row.id }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          //重新获取列表的接口
          this.getList()
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    edit(row) {
      this.modelType = 1
      this.dialogVisible = true
      this.row = row
    },
    getList() {
      getUser({ params: { name: this.input } }).then(({ data }) => {
        this.tableData = data.list
      })
    }
  },
  mounted() {
    this.getList()
  }
}
</script>

<style scoped>
.right {
  display: flex;
}

.col {
  display: flex;
  justify-content: space-between;
  margin: 10px 15px;
}

.ipt {
  margin-right: 5px;
}
</style>