<template>
  <!-- 
    1.安装echarts: npm install echarts --save
    2.在页面script引入echarts: import * as echarts from 'echarts'
    3.创建div容器
    4.echarts.init初始化实例
    5.使用myChart.setOption在optionsChart更新图表
   -->
  <div class="echartBox" ref="echartId" id="echartbox" :style="{ height: height }"></div>
</template>

<script>
import * as echarts from 'echarts'
export default {
  props: {
    optionsChart: {
      require,
      type: Object
    },
    height: {
      type: String,
      default: '300px'
    }
  },
  data() {
    return {

    }
  },
  mounted() {
    var _this = this
    setTimeout(() => {
      _this.chartInit();
    }, 100);
  },
  methods: {
    chartInit() {
      var _this = this
      this.myChart = echarts.init(this.$refs.echartId)
      // this.myChart = echarts.init(document.querySelector('#echartbox'))
      this.myChart.setOption(_this.optionsChart)
    },
  }
}
</script>

<style>
.echartBox {
  width: 100%;
}
</style>