<template>
  <div class="headerContainer">
    <div class="l-content">
      <el-button style="margin-right:20px" @click="handleMenu" icon="el-icon-menu" size="mini"></el-button>
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item class=" text" :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item class="text" v-for="(i, index) in paths" :key="index"
          :to="{ path: '/' + paths.slice(0, 2).join('/') }">{{ i }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="r-content">
      <el-dropdown @command="handleCommand">
        <span class="el-dropdown-link">
          <img class="user"
            src="https://pic.rmb.bdstatic.com/bjh/3ea196e827ed/250514/8a16b4a24afd79bff6020fc91ffffe49.png" alt="">
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="person">个人中心</el-dropdown-item>
          <el-dropdown-item command="exit">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    paths() {
      return this.$route.path.split('/').filter(path => path !== '');
    }
  },
  methods: {
    handleMenu() {
      this.$store.commit('collapseMenu')
    },
    handleCommand(command) {
      if (command === 'exit') {
        localStorage.clear()
        this.$router.push('/')
      }
    },
  }
}
</script>

<style scoped>
.headerContainer {
  padding: 0 20px;
  background-color: #333;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

::v-deep .text span {
  color: #fff !important
}


.l-content {
  display: flex;
  align-items: center;
}

.l-content .el-breadcrumb__item .el-breadcrumb__inner {
  font-weight: normal;
}

.l-content .el-breadcrumb__item .el-breadcrumb__inner.is-link {
  color: rgb(217, 217, 217);
}

.l-content .el-breadcrumb__item:last-child .el-breadcrumb__inner {
  color: #FFF;
}

.r-content .user {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
</style>
