<template>
  <el-row>
    <el-col :span="8">
      <el-card class="box-card">
        <div class="user">
          <img src="https://pic.rmb.bdstatic.com/bjh/3ea196e827ed/250514/8a16b4a24afd79bff6020fc91ffffe49.png" alt="">
          <div class="userinfo">
            <p class="name">{{ name }}</p>
            <p class="access">{{ role }}</p>
            <p></p>
          </div>
        </div>
        <div class="login-info">
          <p>上次登陆地点： <span>广东 珠海</span></p>
          <p>上次登陆时间： <span>20231224</span></p>
        </div>
      </el-card>
      <el-card style="margin-top: 5px; ">
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="name" label="课程" />
          <el-table-column prop="todayBuy" label="今日购买" />
          <el-table-column prop="monthBuy" label="本月购买" />
          <el-table-column prop="totalBuy" label="总购买" />
        </el-table>
      </el-card>
    </el-col>
    <el-col class="over" :span="16">
      <div class="num bg">
        <el-card class="numCard" v-for="item in countData" :key="item.name" shadow="always"
          :body-style="{ display: 'flex', padding: 0 }">
          <i class="icon" :class="`el-icon-${item.icon}`" :style="{ background: item.color }"></i>
          <div class="text">
            <p>{{ item.name }}</p>
            <p>{{ item.value }}</p>
          </div>
        </el-card>
      </div>
      <div class="c">
        <Chart :optionsChart="option" height="260px" class="bg wid"></Chart>
      </div>
      <div class="d">
        <Chart class="bg" :optionsChart="option2" height="250px"></Chart>
        <Chart class="bg" :optionsChart="option3" height="250px"></Chart>
      </div>
    </el-col>
  </el-row>
</template>
<script>
import { getData } from '@/api'
import Chart from './Chart.vue';
export default {
  name: 'Home',
  data() {
    return {
      countData: [
        {
          name: "今日支付订单",
          value: 1234,
          icon: "success",
          color: "#2ec7c9",
        },
        {
          name: "今日收藏订单",
          value: 210,
          icon: "star-on",
          color: "#ffb980",
        },
        {
          name: "今日未支付订单",
          value: 1234,
          icon: "s-goods",
          color: "#5ab1ef",
        },
        {
          name: "本月支付订单",
          value: 1234,
          icon: "success",
          color: "#2ec7c9",
        },
        {
          name: "本月收藏订单",
          value: 210,
          icon: "star-on",
          color: "#ffb980",
        },
        {
          name: "本月未支付订单",
          value: 1234,
          icon: "s-goods",
          color: "#5ab1ef",
        },
      ],
      name: '',
      role: '',
      tableData: [],
      option: {
        grid: [{ bottom: '20%' }],
      },
      option2: {
        tooltip: {
          trigger: 'axis',
          axisPointer: { type: 'cross' }
        },
        legend: {
          type: 'plain',
          show: true,
          align: 'auto',
          textStyle: {
            color: "#333",
          },

        }
      },
      option3: {},
    }
  },
  methods: {
    initChart() {
      getData().then(res => {
        this.tableData = res.data.data.tableData
        const { orderData, userData, videoData } = res.data.data

        //折线图
        const xAxis = Object.keys(orderData.data[0])
        this.option.xAxis = { data: xAxis }
        this.option.yAxis = {}
        this.option.series = xAxis.map(brand => ({
          stack: 'x',
          type: 'line',
          data: orderData.data.map(num => num[brand] || 0)
        }));

        //柱状图
        this.option2.xAxis = { data: userData.map(i => { return i.date }) }
        var key = 'date'
        const keys = Object.keys(userData[0])
        keys.forEach((i, index) => {
          if (i === key) {
            keys.splice(i, 1);
          }
        })
        this.option2.yAxis = {}
        this.option2.series = keys.map(item => {
          return {
            type: 'bar',
            data: userData.map(i => { return i[item] })
          }
        })

        //饼状图
        this.option3.series = [
          {
            type: 'pie',
            data: videoData
          }
        ]
      })
    }
  },
  components: {
    Chart
  },
  mounted() {
    this.initChart()
    this.name = localStorage.getItem('name')
    this.role = this.name == 'admin' ? '管理员' : '游客'
  },
}
</script>
<style scoped>
.wid {
  width: 100%;
  overflow: auto;
  overflow-y: hidden;
}

.over {
  overflow: hidden;
}

.bg {
  background-color: #fff;
  margin: 0 0 5px 5px;
}

.d {
  display: flex;
  overflow: auto;

  /* flex-wrap: wrap; */
}

.icon {
  height: 50px;
  width: 50px;
  color: white;
  font-size: 25px;
  line-height: 50px;
}

.numCard {
  min-width: 230px;
  margin: 10px 5px;
}

.text {
  display: flex;
  flex-direction: column;
  padding-left: 10px;
  justify-content: center;
}

p {
  margin: 0;
  /* padding: 5px; */
}

.num {
  justify-content: space-around;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}


.user {
  display: flex;
  align-items: center;
  padding-bottom: 20px;
  margin-bottom: 20px;
}

.userinfo .access {
  color: #999999;
}

.userinfo .name {
  font-size: 32px;
  margin-bottom: 10px;
}

.user img {
  margin-right: 40px;
  width: 150px;
  height: 150px;
  border-radius: 50%;
}

.login-info p {
  line-height: 28px;
  font-size: 14px;
  color: #999999;
}

.login-info p span {
  color: #666;
  margin-left: 60px;
}
</style>
