<template>
  <div v-if="loading">Loading...</div>

  <el-container class="container" v-else>
    <el-aside class="aside" :width="$store.state.tab.isCollapse ? '64px' : '200px'">
      <Aside class="fix" :menuList="menuList" />
    </el-aside>
    <el-container class="content">
      <Header class="header"></Header>
      <tag></tag>
      <el-main class="main">
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>

</template>

<script>
import Aside from '../components/Aside.vue'
import Header from '../components/Header.vue'
import Tag from '../components/tag.vue'
export default {
  components: {
    Aside, Header, Tag
  },
  data() {
    return {
      loading: true,
      menuList: []
    }
  },
  created() {
    console.log('123', localStorage.getItem('menus'));

    setTimeout(() => {
      const menuList = localStorage.getItem('menus');
      this.menuList = menuList ? JSON.parse(menuList) : [];
      this.loading = false; // 加载完成
    }, 300); // 可移除延迟，仅用于演示
  },
  methods: {
    clearSession() {
      localStorage.clear()
      sessionStorage.clear()
    }
  },
  mounted() {
    // window.addEventListener('beforeunload', this.clearSession)
  }
}
</script>

<style scoped>
.aside {
  height: 100%;
  max-width: 200px;
  position: relative;
}

el-aside {
  height: 100%;
}

.isCollapse {
  width: 64px;
}

.container {
  display: flex;
  height: 100%;
  flex: 1;
  background-color: rgba(65, 65, 65, 0.1);

}


.main {
  padding: 0;
  /* overflow: hidden; */
}

.content {
  display: flex;
  flex-direction: column;
}
</style>