<template>
  <div class="tagBox">
    <el-tag class="tag" size="medium" :closable="item.name != 'home'" v-for="(item, index) in tags" :key="item.path"
      @close="handleClose(item, index)" @click="clickTag(item)">{{ item.label }}</el-tag>
  </div>
</template>
<script>
import { mapMutations, mapState } from 'vuex';
export default {
  data() {
    return {

    };
  },
  computed: {
    ...mapState({
      tags: state => state.tab.tabsList
    })
  },
  methods: {
    ...mapMutations(['closeTag']),
    handleClose(item, index) {
      this.closeTag(item)
      if (item.path != this.$route.path) return
      this.$router.push(this.tags[index - 1].path)
    },
    clickTag(item) {
      this.$router.push(item.path)
    }
  }
}
</script>

<style>
.tag {
  margin-right: 5px;
}

.tagBox {
  display: flex;
  justify-content: start;
}
</style>
