<template>
  <el-menu :default-active="activePath" class="el-menu-vertical el-menu" background-color="#545c64" text-color="#fff"
    active-text-color="#ffd04b" router :collapse="isCollapse" @select="clickMenu">
    <h3>{{ isCollapse ? '后台' : 'XXXXXX系统' }}</h3>
    <template v-for="item in menuList">
      <el-submenu v-if="item.children && item.children.length" :key="item.path || item.name" :index="item.path">
        <template slot="title">
          <i :class="`el-icon-${item.icon}`"></i>
          <span slot="title">{{ item.label }}</span>
        </template>
        <el-menu-item-group v-for="subItem in item.children" :key="subItem.path">
          <el-menu-item :index="subItem.path">
            <i :class="['el-icon-' + subItem.icon]"></i>
            <span>{{ subItem.label }}</span>
          </el-menu-item>
        </el-menu-item-group>
      </el-submenu>

      <!-- 普通菜单项 -->
      <el-menu-item v-else :key="item.path || item.name" :index="item.path">
        <i :class="['el-icon-' + item.icon]"></i>
        <span slot="title">{{ item.label }}</span>
      </el-menu-item>
    </template>
  </el-menu>
</template>

<script>
export default {
  name: 'Aside',
  props: {
    menuList: {
      type: Array,
      required: true
    },
    activePath: {
      type: String,
      default: ''
    }
  },

  components: {
  },
  computed: {
    isCollapse() {
      return this.$store.state.tab.isCollapse
    }
  },
  methods: {
    clickMenu(key, keyPath) {
      // const select = (List) => {
      //   return List.filter(i => {
      //     if (i.children) {
      //       select(i.children)
      //     }
      //     return i.path == key
      //   })
      // }
      const findItem = (list, path) => {
        for (const item of list) {
          if (item.path === path) return item;
          if (item.children) {
            const found = findItem(item.children, path);
            if (found) return found;
          }
        }
        return null;
      };
      const selectedItem = findItem(this.menuList, key);
      this.$store.commit('selectTabs', selectedItem)
    }
  }
}
</script>

<style scoped>
h3 {
  color: #fff;
}

.el-menu-vertical {
  overflow: hidden;
  height: 100%;
}

.el-menu-vertical-demo:not(.el-menu--collapse) {
  /* min-height: 400px; */
  height: 100%;
}

.el-menu-item,
::v-deep .el-submenu__title {
  text-align: start;
}

.el-menu {
  border-right: none;
  height: 100%;
}

.el-menu h3 {
  line-height: 48px;
  font-size: 16px;
  font-weight: 400;
}
</style>