import Mock from 'mockjs'
export default {
  Login: config => {
    const { username, password } = JSON.parse(config.body)
    // 先判断用户是否存在
    // 判断账号和密码是否对应
    if (username === 'admin' && password === 'admin') {
      return {
        code: 20000,
        data: {
          role: 'admin',
          token: Mock.Random.guid(),
          message: '登录成功'
        }
      }
    } else if (password === '123') {
      return {
        code: 20000,
        data: {
          role: 'guest',
          token: Mock.Random.guid(),
          message: '获取成功'
        }
      }
    } else {
      return {
        code: -999,
        data: {
          message: '密码错误'
        }
      }
    }
  }
}
