export default {
  state: {
    isCollapse: false,
    tabsList: [{
      path: '/',
      name: 'home',
      label: '首页',
      icon: 's-home',
      url: 'Home/Home'
    }]
  },
  mutations: {
    collapseMenu(state) {
      state.isCollapse = !state.isCollapse
    },
    selectTabs(state, val) {
      if (val != '/' || val != 'home') {
        const index = state.tabsList.findIndex(i => i.name == val.name)
        if (index == -1) {
          state.tabsList.push(val)
        }
      }
    },
    closeTag(state, val) {
      const index = state.tabsList.findIndex(i => i.name == val.name)
      state.tabsList.splice(index, 1)
    }
  }
}
